    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: $bg-theme-color;

    .menu {
        position: relative;
        margin-top: 2rpx;
        background-color: #ffffff;
        padding: 0 30rpx;

        .filter-container {
            display: flex;
            align-items: center; // ÂûÇÁõ¥Â±Ö‰∏≠
            justify-content: space-around; // Ê∞¥Âπ≥Âπ≥ÂàÜÁ©∫Èó¥
            height: 88rpx;

            .filter-item {
                display: flex;
                align-items: center;
                gap: 10rpx; // ÊñáÂ≠óÂíåÁÆ≠Â§¥Á¥ßÊå®ÁùÄ
                min-height: 60rpx;

                text {
                    font-size: 28rpx;
                    color: #333;
                }
            }

        }
    }

    .tjxx {
        display: flex;
        justify-content: space-between;
        height: 132rpx;

        margin: 30rpx;
        gap: 20rpx;

        .tj-item {
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: space-around;
            background: #fff;
            border-radius: 20rpx;

            image {
                width: 88rpx;
                height: 88rpx;
            }

            .ljts {
                .number {
                    font-size: 30rpx;
                    font-weight: bold;
                    color: #3D3D3D;
                }

                .title {
                    font-size: 24rpx;
                    color: #3D3D3D;
                }
            }
        }
    }

    .content-wrapper {
        flex: 1;

        .content {
            height: 100%;
        }

        .order-list {
            padding: 0 30rpx; // Â∑¶Âè≥30rpx

            .order-item {
                margin-bottom: 30rpx;
                padding: 30rpx;
                background-color: #fff;
                border-radius: 12rpx;

                .order-header {
                    margin-bottom: 20rpx;

                    .shop-info {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        margin-bottom: 16rpx;

                        .shop-name {
                            font-size: 28rpx;
                            font-weight: 400;
                            color: rgba(61, 61, 61, 1);
                        }

                        .status-tag {
                            font-size: 24rpx;
                            width: 120rpx;
                            height: 40rpx;
                            border-radius: 8rpx;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            text-align: center;


                            &.processing {
                                //ËøõË°å‰∏≠ ÂæÖÂÆåÊàê
                                color: rgba(0, 170, 255, 1);
                                background: rgba(0, 170, 255, 0.10);
                            }

                            &.completed {
                                //Â∑≤ÂÆåÊàê
                                color: rgba(61, 61, 61, 0.50);
                                background: rgba(153, 153, 153, 0.1);
                            }

                            &.cancelled {
                                //Êó†Ê≥ïÊî∂Ëøê
                                color: rgba(255, 161, 0, 1);
                                background: rgba(255, 161, 0, 0.10);

                            }
                        }
                    }


                }

                .order-content {
                    padding: 20rpx 0;
                    border-top: 1px solid #f0f0f0;
                    border-bottom: 1px solid #f0f0f0;

                    .info-item {
                        display: flex;
                        margin-bottom: 16rpx;

                        &:last-child {
                            margin-bottom: 0;
                        }

                        .label {
                            font-size: 26rpx;
                            color: rgba(61, 61, 61, 0.50);
                        }

                        .value {
                            margin-left: 30rpx;
                            font-size: 26rpx;
                            color: rgba(61, 61, 61, 1);
                        }
                    }
                }

                .order-footer {
                    display: flex;
                    justify-content: flex-end;
                    margin-top: 20rpx;

                    .btn-cancel {
                        margin-right: 20rpx;
                        color: rgba(61, 61, 61, 1);
                        background-color: #fff;
                        border: 1px solid rgba(196, 196, 196, 1);
                        font-size: 26rpx;
                        width: 144rpx;
                        height: 48rpx;
                        border-radius: 20rpx;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        box-sizing: border-box; // ‰ΩøÁî®border-boxÁõíÊ®°Âûã

                    }

                    .btn-confirm {
                        color: rgba(7, 193, 96, 1);
                        // background-color: rgba(7, 193, 96, 1);
                        border: 1rpx solid rgba(7, 193, 96, 1);
                        font-size: 26rpx;
                        width: 144rpx;
                        height: 48rpx;
                        border-radius: 20rpx;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        box-sizing: border-box; // ‰ΩøÁî®border-boxÁõíÊ®°Âûã
                    }
                }
            }
        }
    }

    .loadMore {
        padding-bottom: calc(env(safe-area-inset-bottom) + 50);
    }

    // ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ
    .empty-state {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        min-height: calc(100vh - 200rpx); // Á°Æ‰øùÂç†Êª°Ââ©‰ΩôÂ±èÂπïÈ´òÂ∫¶
        padding: 120rpx 60rpx;
        text-align: center;
        background-color: #ffffff;

        .empty-icon {
            font-size: 120rpx;
            margin-bottom: 30rpx;
            opacity: 0.3;
        }

        .empty-text {
            font-size: 32rpx;
            color: rgba(61, 61, 61, 0.6);
            margin-bottom: 16rpx;
            font-weight: 500;
        }

        .empty-desc {
            font-size: 26rpx;
            color: rgba(61, 61, 61, 0.4);
            line-height: 1.5;
        }
    }

    // Ëá™ÂÆö‰πâÂØºËà™Ê†èÂ≠ó‰ΩìÂ§ßÂ∞è‰∏∫34rpx
    :deep(.uni-navbar__content-title) {
        font-size: 34rpx !important;
    }

    :deep(.uni-nav-bar-text) {
        font-size: 34rpx !important;
    }
}
</style>